<template>
    <div>
      <el-table :height="400"
        :data="tableData.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
        style="width: 100%">
        <el-table-column
          label="编号"
          width="180">
          <template #default="{ row }">
            <span style="margin-left: 10px">{{ row.id }}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="姓名"
          width="180">
          <template #default="{ row }">
            <el-popover trigger="hover" placement="top">
              <p>姓名: {{ row.name }}</p>
              <p>住址: {{ row.address }}</p>
              <template #reference>
                <div class="name-wrapper">
                  <el-tag size="medium">{{ row.name }}</el-tag>
                </div>
              </template>
            </el-popover>
          </template>
        </el-table-column>
        <el-table-column label="生日" prop="bir"></el-table-column>
        <el-table-column label="性别" prop="sex"></el-table-column>
        <el-table-column label="地址" prop="address"></el-table-column>
        <el-table-column align="right">
          <template #header>
            <el-input
              v-model="search"
              size="mini"
              placeholder="输入姓名的关键字进行搜索"/>
          </template>
          <template #default="{ $index, row }">
            <el-button
              size="mini"
              @click="handleEdit($index, row)">编辑</el-button>
            
              <el-popconfirm
    confirm-button-text="好的"
    cancel-button-text="不用了"
    icon="el-icon-info"
    icon-color="red"
    title="确定要删除当前用户吗？"
    @confirm="handleDelete($index, row)"
  >
    <!-- 触发对话框的按钮 -->
    <template #reference>
      <el-button
        size="mini"
        type="danger"
      >
        删除
      </el-button>
    </template>
  </el-popconfirm>
          </template>
        </el-table-column>
      </el-table>
      <el-button style="margin: 10px 0px;" @click="saveUserInfo" type="success" size="mini">添加</el-button>
      <transition name="el-zoom-in-center">
        <div v-show="show" class="transition-box">
            
          
          <el-form :hide-required-asterisk="false" :rules="rules" ref="userForm" :model="form" label-width="80px" label-suffix=":">
  
    
            <el-form-item label="姓名" prop="name">
        
              <el-input v-model="form.name"></el-input>
  
            </el-form-item>

  
            <el-form-item label="生日" prop="bir">
   
              <el-date-picker type="date" placeholder="选择日期" v-model="form.bir" style="width: 100%;"></el-date-picker>
  
            </el-form-item>

  <el-form-item label="性别">
    <el-radio-group v-model="form.sex">
      <el-radio label="男"></el-radio>
      <el-radio label="女"></el-radio>
    </el-radio-group>
  </el-form-item>
  <el-form-item label="详细地址" prop="address">
    <el-input type="textarea" v-model="form.address"></el-input>
  </el-form-item>
  <el-form-item>
    <el-button type="primary" @click="onSubmit('userForm')">保存信息</el-button>
    <el-button @click="saveUserInfo">重置</el-button>
  </el-form-item>
</el-form></div>
      </transition>
    </div>
  </template>
  
  <script>
  export default {
    data() {
      return {
        tableData: [],
        search: "",
        show:false,
        form: {
          name: '',
          bir:"",
          sex:"男",
          address:"",
        },
        rules: {
          name: [
            { required: true, message: '请输入用户的姓名!', trigger: 'blur' }
          ],
          bir: [
            { required: true, message: '请选择用户的出生年月!', trigger: 'blur' }
          ],
          address: [
            { required: true, message: '请输入用户的地址!', trigger: 'blur' }
          ],
        }
      }
    },
    methods: {
      saveUserInfo(){
        //点击添加信息处理
        this.form={sex:"男"};
        this.show=true; 
      },
      handleEdit(index, row) {
        console.log(index, row);
        //展示编辑表单
        this.show=true;
        //回显编辑信息
        this.form = row;
      },
      handleDelete(index, row) {
        console.log(index, row);
        //发送axios请求 申请删除
        this.$http.delete("http://localhost:8080/user/delete?id=" + row.id).then(res=>{
          if(res.data.status){
            this.$message({
            message: res.data.msg,
            type: 'success'
        });
        //刷新数据
        this.findAllTableData();
          }else {
            this.$message.error(res.data.msg);
          }
        })
      },
      onSubmit(userForm) {
        //信息验证
        this.$refs[userForm].validate((valid) => {
          if (valid) {
            //发送一个axios请求
        this.$http.post("http://localhost:8080/user/saveOrUpdate",this.form).then(res=>{
          console.log(res.data);
          if(res.data.status){
            this.$message({
            message: '恭喜你，'+res.data.msg,
            type: 'success'
        });
        //清空列表信息
        this.form = {sex:"男"};
        //消失
        this.show=false;
        //刷洗
        this.findAllTableData();
          }else {
            this.$message.error(res.data.msg);
          }
        })
          } else {
            this.$message.error("当前输入的数据不合法");
            return false;
          }
        });
        
      },
      findAllTableData(){
        this.$http.get("http://localhost:8080/user/findAll").then(res=>{
            this.tableData=res.data;
        });
      }
    },
    created() {
      // Initialize data if needed
        this.findAllTableData();
    }
  }
  </script>
   
<style scoped>
.transition-box {
    margin-bottom: 10px;
    width: 100%;
    height: 600px;
    border-radius: 4px;
   
    padding: 40px 20px;
    box-sizing: border-box;
    margin-right: 20px;
  }
</style>